@import "@/assets/scss/main.scss";

.tweet__card {
  position: relative;
  display: block;

  &:hover {
    background-color: $scale-gray3;
  }

  .router-link-active {
    text-decoration: none;
    color: $scale-gray10;
  }

  a {
    text-decoration: none;
    color: $scale-gray10;
  }

  .tweet__card__content {
    display: block;
    border-top: 1px solid $border;
    padding: 16px 24px 40px 24px;

    .tweet__card__title {
      display: flex;
      align-items: top;
      height: 40px;

      .tweet__card__avatar {
        border-radius: 50%;
        width: 50px;
        height: 50px;

        &:hover {
          opacity: 0.7
        }
      }

      .tweet__card__name {
        margin: 2px 8px 0 10px;
        font-size: 16px;
        font-weight: 700;
        // &:hover {
        //   text-decoration: solid underline $modal-black 1.5px;
        // }
      }

      .tweet__card__account,
      .tweet__card__time {
        margin: 4px 0 0 0;
        color: $secondary-gray;
        font-size: 14px;
        font-weight: 400;
      }
    }

    .tweet__card__description {
      color: $scale-gray10;
      font-size: 16px;
      font-weight: 400;
      margin: 0;
      padding: 0px 0 5px 60px;
    }
  }

  .tweet__card__footer {
    position: absolute;
    bottom: 16px;
    left: 82px;
    width: 120px;
    height: 16px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: $secondary-gray;
    font-size: 14px;
    font-weight: 600;
    font-family: Montserrat;

    img {
      position: relative;
      display: block;
      width: 13px;
      height: 13px;
      cursor: pointer;
      // &:hover {
      //   border: 10px solid rgba(255, 102, 0, 0.2);
      // }
    }

    // p {
    //   margin: 0 40px 0 10px;
    //   &:hover {
    //     color: $brand-orange;
    //   }
    // }
  }
}
