@import "@/assets/scss/main.scss";

.popularList {
  position: fixed;
  z-index: 990;
  top: 16px;
  right: 9.3vw;
  width: 19.5vw;
  max-width: 350px;
  min-width: 265px;
  background: $scale-gray2;
  border-radius: 16px;

  &--title h1 {
    margin: 24px auto 24px 24px;
    font-weight: 700;
    font-size: 24px;
    line-height: 26px;
  }

  &--users {
    border-top: 1px solid $border;

    .user--wrapper {
      padding: 16px;

      &--image img {
        width: 50px;
        height: 50px;
        border-radius: 50%;
      }

      &--info {
        max-width: 6.4vw;
        margin-left: 8px;

        h3 {
          overflow: hidden;
          text-overflow: ellipsis;
          color: $scale-gray10;
          font-weight: 700;
          font-size: 16px;
          line-height: 26px;
          // background: green;
        }

        p {
          overflow: hidden;
          text-overflow: ellipsis;
          color: $scale-gray7;
          font-weight: 500;
          font-size: 14px;
          line-height: 22px;
          // background: blue;
        }
      }

      &--button {
        min-width: 96px;
        height: 40px;

        .btn {
          border-radius: 50px;
          // padding: 8px 1vw;
          margin-left: 5px;
          font-weight: 400;
          font-size: 16px;

          // line-height: 24px;
          &-follow {
            width: 100%;
            background: $brand-orange;
            color: $scale-gray1;
          }

          &-unfollow {
            width: 67%;
            background: $scale-gray1;
            border: 1px solid $brand-orange;
            color: $brand-orange;
          }
        }
      }
    }
  }
}
