@import "@/assets/scss/main.scss";

.sidebar {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 9.3vw;
  z-index: 999;
  width: 12.7vw;
  max-width: 178px;
  min-width: 160px;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding-bottom: 16px;
  background: $scale-gray1;

  .sidebar--logo {
    padding: 8px 0 0 8px;
    margin-bottom: 30px;
  }

  .sidebar--menu {
    padding-left: 16px;
    a:not(:last-child) {
      margin-bottom: 40px;
    }
    img {
      margin-right: 16px;
    }
    span {
      color: $scale-gray9;
      font-weight: 700;
      font-size: 18px;
      line-height: 26px;
    }
  }
  .icon-logo {
    width: 50px;
    height: 50px;
  }
  .icon {
    width: 24px;
    height: 24px;
  }
  button {
    width: 100%;
    height: 46px;
    border-radius: 50px;
    background: $brand-orange;
    font-size: 20px;
    color: $scale-gray1;
    padding: 8px 0;
    margin-top: 24px;
  }
  
  .router-link-exact-active {
    span {
      color: $brand-orange;
    }
  }

}
