@import "@/assets/scss/main.scss";

.modal-mask {
  position: fixed;
  z-index: 999;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .5);
  display: table;
  transition: opacity .4s ease;
  padding: 56px 30.5vw 300px 23.7vw;
}

.modal-wrapper {
  padding: 56px 30.5vw auto 23.7vw;
}

.modal-container {
  width: 45.7vw;
  background-color: $scale-gray1;
  border-radius: 14px;
  transition: all .2s ease;

  .modal-header {
    padding: 20.5px 16px 20.5px 19.5px;

    img {
      width: 15px;
      height: 15px;
      // padding: 14px 0;
      border-bottom: 1px solid $border;
    }
  }

  .modal-body {
    padding: 16px 0 0 24px;

    img {
      max-width: 50px;
      height: 50px;
      border-radius: 50%;
      object-fit: cover
    }

    textarea {
      width: 100%;
      height: 100%;
      padding: 12px 24px 12px 8px;
      border: none;
      color: $scale-gray10;
      font-weight: 400;
      font-size: 18px;
      line-height: 26px;
      resize: none;
      overflow-y: scroll;

      &::placeholder {
        color: $secondary-gray;
        font-weight: 400;
        font-size: 18px;
        line-height: 26px;
      }
    }
  }

  .modal-footer {
    border-top: none;

    button {
      height: 46px;
      border-radius: 50px;
      background: $brand-orange;
      font-size: 20px;
      color: $scale-gray1;
      padding: 8px 24px;
      margin: 0 16px 16px 0;

      &.disabled {
        opacity: 0.5;
      }
    }

    span {
      color: $error-red;
      font-weight: 500;
      margin: 0 16px 16px 0;
      font-size: 15px;
      line-height: 15px;
    }
  }
}

.modal-enter {
  opacity: 0;
}

.modal-leave-active {
  opacity: 0;
}

.modal-enter .modal-container,
.modal-leave-active .modal-container {
  -webkit-transform: scale(0.95);
  transform: scale(0.95);
}
